
import { Vue } from "vue-class-component";

export default class EditableModal extends Vue {
  private internalIsShow = false;

  public show() {
    this.internalIsShow = true;
  }

  public close() {
    this.internalIsShow = false;
    this.$emit("close", null);
  }

  private save() {
    this.close();
    this.$emit("save", null);
  }
}
